import { mapState } from 'vuex';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	data() {
		return {
			type: 0,
			id: -1,
			activity: null,
			author: null,
			attention: 0,
			is_collect: 0,
			is_like: 0,
			is_baoming: 0,
			max: 1,
			height: 0,
			show: false,
			pl: false,
			comment: {
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			},
			reply: {
				comment: {},
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			},
			replyId: -1,
			to_user_name: '',
			showInput: false,
			showInput1: false,
			content: '',
			content1: ''
		};
	},
	computed: {
		...mapState(['isLogin', 'userInfo'])
	},
	components: { uniPopup },
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		this.$Fetch({
			url: 'activity/share',
			data: { activity_id: this.id },
			success: res => {
				let str = res>0 ? '已分享,砖瓦+' + res : '已分享';
				uni.showToast({
					title: str,
					icon: 'none'
				});
			}
		});
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/activity/activity?id='+this.id+'&server=' + user.server_people_id+'&path=/pages/index/activity/activity'
			};
		}
	},
	onLoad(opt) {
		let token = uni.getStorageSync('TOKEN');
		let that = this;
		if (!token){
			this.setShareMsg({
				server: opt.server,
				path: opt.path+'?id='+opt.id
			});
			uni.redirectTo({
				url: '/pages/login/login'
			});
			return false
		}
		uni.getSystemInfo({
			success: function(res) {
				that.height = res.windowHeight / 2 + 'px';
			}
		});
		console.log('活动ID=', opt.id);
		if (opt.id) {
			this.id = opt.id;
			this.getActivity(opt.id);
			this.getCommentList();
		}
	},
	onReachBottom() {
		this.getCommentList();
	},
	onShow() {
		if (this.id > -1) {
			this.getActivity(this.id);
		}
	},
	methods: {
		videoErrorCallback() {},
		doShare() {
			let that=this;
			uni.share({
				provider: 'weixin',
				scene: "WXSceneSession", //WXSceneSession”分享到聊天界面，“WXSenceTimeline”分享到朋友圈，“WXSceneFavorite”分享到微信收藏
				type: 5, //0-图文；5-小程序
				imageUrl:'../../static/poster.png',
				title: '欢迎使用易建联APP',
				miniProgram: {
					id:'gh_4bf03e4d36fb',
					path:'/pages/index/activity/activity?id='+this.id+'&server=' + this.userInfo.server_people_id+'&path=/pages/index/activity/activity',
					webUrl:'https://uniapp.dcloud.io',
					type:2
				},
				success: e => {
					console.log('success', e);
					that.$Fetch({
						url: 'activity/share',
						data: { activity_id: that.id },
						success: res => {
							let str = res>0 ? '已分享,砖瓦+' + res : '已分享';
							uni.showToast({
								title: str,
								icon: 'none'
							});
						}
					});
				},
				fail: e => {
					console.log('fail', e);
				},
				complete: function() {
					console.log('分享操作结束!');
				}
			});
		},
		//评论
		send() {
			if(!this.content.trim()){
				uni.showModal({
					content: '请输入内容',
					showCancel: false
				});
				return false;
			}
			this.$Fetch({
				url: 'Comment/add',
				data: {
					comment_type: 0,
					id_value: this.id,
					content: this.content
				},
				success: res => {
					console.log(res);
					// let comment=this.comment
					let date = new Date();
					let m = date.getMonth() + 1 > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1);
					let d = date.getDate() > 9 ? date.getDate() : '0' + date.getDate();
					let time = date.toTimeString().split(' ')[0];
					console.log(time);
					this.comment.list.unshift({
						user_id: this.userInfo.id,
						user_name: this.userInfo.user_name,
						content: this.content,
						create_time: date.getFullYear() + '-' + m + '-' + d + ' ' + time,
						like_num: 0,
						head_img: this.userInfo.head_img,
						children: null,
						children_num: 0,
						is_like: 0
					});
					this.content = '';
					uni.hideKeyboard();
					this.showInput = false;
				}
			});
		},
		doReply(id, user_name) {
			this.showInput1 = true;
			this.replyId = id;
			this.to_user_name = user_name;
		},
		send1() {
			if(!this.content1.trim()){
				uni.showModal({
					content: '请输入内容',
					showCancel: false
				});
				return false;
			}
			let parent_id = this.reply.comment.id;
			this.$Fetch({
				url: 'Comment/addComment',
				data: {
					parent_id: parent_id,
					to_user_id: this.replyId,
					content: this.content1
				},
				success: res => {
					console.log(res);
					// let comment=this.comment
					let date = new Date();
					let m = date.getMonth() + 1 > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1);
					let d = date.getDate() > 9 ? date.getDate() : '0' + date.getDate();
					let time = date.toTimeString().split(' ')[0];
					console.log(time);
					if (this.replyId) {
						console.log('回复评论');
						this.reply.list.unshift({
							user_id: this.userInfo.id,
							user_name: this.userInfo.user_name,
							content: this.content1,
							create_time: date.getFullYear() + '-' + m + '-' + d + ' ' + time,
							like_num: 0,
							head_img: this.userInfo.head_img,
							is_like: 0,
							to_user_name: this.to_user_name
						});
					} else {
						console.log('回复回复');
						this.reply.list.unshift({
							user_id: this.userInfo.id,
							user_name: this.userInfo.user_name,
							content: this.content1,
							create_time: date.getFullYear() + '-' + m + '-' + d + ' ' + time,
							like_num: 0,
							head_img: this.userInfo.head_img,
							is_like: 0,
							to_user_name: this.to_user_name
						});
					}

					this.content1 = '';
					uni.hideKeyboard();
					this.showInput1 = false;
				}
			});
		},
		getActivity(id) {
			this.$Fetch({
				url: 'activity/getDataById',
				data: { id: id },
				success: res => {
					console.log(res);
					this.activity = res;
					this.author = res.adminUser;
					this.attention = res.attention;
					this.is_collect = res.is_collect;
					this.is_like = res.is_like;
					this.is_baoming = res.is_baoming;
					this.max = res.activity_num - res.people_num;
					let start = res.start_time.replace(/\-/g, '/');
					let end = res.end_time.replace(/\-/g, '/');
					let now = new Date().getTime();
					if (now < new Date(start).getTime()) {
						this.type = 0;
					} else if (now <= new Date(end).getTime()) {
						this.type = 1;
					} else {
						this.type = 2;
					}
				}
			});
		},
		//关注
		doAttention(id) {
			console.log('关注', id);
			this.$Fetch({
				url: 'Attention/add',
				data: { id: id },
				debug: true,
				success: res => {
					console.log(res);
					this.attention = res.success;
					let str = res.success === 1 ? '已关注,砖瓦+' + res.point : '已取消关注';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//收藏
		doCollect(type) {
			this.$Fetch({
				url: 'Collect/add',
				data: {
					collect_type: 1,
					id_value: this.id
				},
				debug: true,
				success: res => {
					console.log(res);
					this.is_collect = res.success;
					let str = res.success === 1 ? '已收藏,砖瓦+' + res.point : '已取消收藏';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//文章点赞
		doZan(type) {
			this.$Fetch({
				url: 'user_like/add',
				data: {
					like_type: 1,
					id_value: this.id
				},
				success: res => {
					console.log(res);
					this.is_like = res.success;
					let str = res.success === 1 ? '已点赞,砖瓦+' + res.point : '已取消点赞';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//获取评论列表
		getCommentList() {
			let data = this.comment;
			if (data.loading) {
				data.loading = false;
				this.comment = data;
				this.$Fetch({
					url: 'Comment/getData',
					data: {
						comment_type: 0,
						id_value: this.id,
						pageNum: data.pageNum,
						pageSize: data.pageSize
					},
					success: res => {
						let arr = res.lists;
						if (arr.length > 0) {
							data.list = data.list.concat(arr);
							if (arr.length === data.pageSize) {
								data.pageNum++;
								data.loading = true;
								data.more = true;
							} else {
								data.more = false;
							}
						} else {
							data.more = false;
						}
						this.comment = data;
					}
				});
			}
		},
		//评论点赞
		addCommentLike(item, index) {
			this.$Fetch({
				url: 'Comment/addCommentLike',
				data: { id: item.id },
				success: res => {
					item.is_like = res;
					if (res === 1) {
						item.like_num++;
					} else {
						item.like_num--;
					}
				}
			});
		},
		closePopup() {
			this.pl = false;
			this.reply = {
				comment: {},
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			};
		},
		showPopup(item) {
			this.getReply(item);
			this.pl = true;
		},
		getReply(item) {
			let data = this.reply;
			data.comment = item;
			this.reply = data;
			if (data.loading) {
				data.loading = false;
				this.reply = data;
				this.$Fetch({
					url: 'Comment/getComment',
					data: {
						id: item.id,
						pageNum: data.pageNum,
						pageSize: data.pageSize
					},
					debug: true,
					success: res => {
						let len = res.length;
						if (len > 0) {
							data.list = data.list.concat(res);
							if (len === data.pageSize) {
								data.pageNum++;
								data.loading = true;
								data.more = true;
							} else {
								data.more = false;
							}
						} else {
							data.more = false;
						}
						this.reply = data;
						this.pl = true;
					}
				});
			}
		},
		onSubmit(e) {
			console.log('提交数据：', e.detail.value);
			this.$Fetch({
				url: 'activity_list/add',
				data: e.detail.value,
				success: res => {
					console.log(res);
					uni.navigateTo({
						url: '../success/success?id=' + res
					});
				}
			});
		}
	}
};
